import usePlacesAutocomplete, { getGeocode, getLatLng } from "use-places-autocomplete";
import { Col, Input, Row, Popover } from "antd";
import './Map.scss'

const PlacesAutocomplete = ({ setPosition = () => { } }) => {
    const {
        ready,
        value,
        suggestions: { status, data },
        setValue,
        clearSuggestions,
    } = usePlacesAutocomplete();


    const handleSelect = async (address) => {
        setValue(address, false);
        clearSuggestions();

        const results = await getGeocode({ address });
        const { lat, lng } = await getLatLng(results[0]);
        setPosition({ lat, lng });
    }

    return (
        <Col style={{ paddingRight: 20 }} span={24}>
            <Row>
                <Popover
                    style={{ width: '100%' }}
                    content={status === 'OK' && <Col>{data.map(({ place_id, description }) => {
                        return <Row className="address-suggestion" key={place_id} onClick={() => handleSelect(description)}>{description}</Row>
                    })}</Col>}
                    title="Title"
                    trigger="click"
                    open={status === 'OK'}
                    placement="bottomLeft"
                    arrow={false}
                >
                    <Input style={{ width: '100%' }} placeholder="Tm kim a im mong mun ti y" value={value} disabled={!ready} allowClear={true} onChange={(e) => {
                        setValue(e.target.value)
                    }}
                    />
                </Popover>
            </Row>
            {value && <> <Row style={{ paddingTop: 10, fontWeight: 'bold' }}>
                Thng tin chi tit
            </Row>
                <Row >
                    <Col style={{ width: 120 }}>a ch:</Col>
                    <Col>
                        {value}
                    </Col>
                </Row></>}
        </Col>
    );
};

export default PlacesAutocomplete;